import request from '../utils/request'

export const login = (data) => {
  return request({
    url: '/auth/login',
    method: 'post',
    data
  })
}

export const surlsList = (params) => {
  return request({
    url: '/surls',
    method: 'get',
    params
  })
}

export const surlsAdd = (data) => {
  return request({
    url: '/surls',
    method: 'post',
    data
  })
}

export const surlsDel = (id) => {
  return request({
    url: `/surls/${id}`,
    method: 'delete'
  })
}
