<template>
  <div>
    <el-dialog
      title="创建短链"
      :visible.sync="dialogVisible"
      width="500px"
      v-if="dialogVisible"
      @close="close"
    >
      <el-form ref="form" label-width="80px" :model="form" :rules="rules">
        <el-form-item label="原链接" prop="url">
          <el-input size="small" v-model="form.url"></el-input>
        </el-form-item>
        <el-form-item>
          <div class="footer">
            <el-button type="primary" @click="submitForm" size="mini"
              >立即创建</el-button
            >
            <el-button @click="resetForm" size="mini">重置</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { surlsAdd } from '@/api'
export default {
  data () {
    return {
      dialogVisible: false,
      labelPosition: 'right',
      form: {
        url: ''
      },
      rules: {
        url: [{ required: true, message: '请输入原链接', trigger: 'blur' }]
      }
    }
  },
  methods: {
    open () {
      this.dialogVisible = true
    },
    close () {
      this.form.url = ''
      this.dialogVisible = false
      this.$emit('refresh')
    },
    submitForm () {
      this.$refs.form.validate((valid) => {
        if (!valid) return false
        console.log('form>>>', this.form)
        surlsAdd(this.form).then((res) => {
          if (res.code === 0) {
            this.$message.success(res.msg)
            this.close()
          }
        })
      })
    },
    resetForm () {
      this.form.url = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.footer {
  display: flex;
  justify-content: flex-end;
}
</style>
