<template>
  <div class="login">
    <el-card class="login_form" shadow="always" v-loading="loading">
      <div class="name">
        <div class="title">短 链 服 务</div>
        <div class="sub_title">Short chain service By golang</div>
      </div>
      <el-form ref="form" :model="form">
        <el-form-item prop="phone">
          <el-input
            autocomplete="on"
            prefix-icon="el-icon-user"
            type="string"
            placeholder="请输入邮箱"
            v-model.trim="form.email"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            autocomplete="on"
            prefix-icon="el-icon-lock"
            type="password"
            :show-password="true"
            placeholder="请输入密码"
            v-model.trim="form.password"
            @keyup.enter.native="onSubmit"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <div class="btn">
            <el-button type="primary" :loading="loading" @click="onSubmit"
              >立即登录</el-button
            >
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { login } from '@/api'
import { Token } from '@/utils/storage'
export default {
  data () {
    return {
      loading: false,
      form: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    onSubmit () {
      this.$refs.form.validate(async (valid) => {
        if (!valid) return false
        this.loading = true
        await login(this.form).then((res) => {
          if (res.code === 0) {
            console.log('Response data:', res.data)
            Token.set(res.data.token)
            this.$router.push('/')
          }
        })
        this.loading = false
      })
    }
  }
}
</script>

