<template>
  <div>
    <div class="content">
      <div class="oper">
        <el-button @click="onAdd" size="mini" type="success">添加</el-button>
        <el-button size="mini" type="danger" @click="logout"
          >退出登陆</el-button
        >
      </div>
      <el-table size="small" :data="tableData" border style="width: 100%">
        <el-table-column prop="id" label="ID" width="100"> </el-table-column>
        <el-table-column prop="url" label="原地址">
          <template slot-scope="scope">
            <el-link :underline="false" type="primary">{{
              scope.row.url
            }}</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="surl" label="短链">
          <template slot-scope="scope">
            <el-link :underline="false" type="success">{{
              scope.row.surl
            }}</el-link>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150px">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="paging">
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="limit"
            layout="total, sizes, prev, pager, next, jumper"
            :total="count"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <add-url ref="add" @refresh="loadData"></add-url>
  </div>
</template>

<script>
import { surlsList, surlsDel } from '@/api'
import AddUrl from '@/components/Add'
export default {
  name: 'Surls',
  components: {
    AddUrl
  },
  data () {
    return {
      count: 0,
      page: 1,
      limit: 10,
      tableData: [
        {
          id: 1,
          url: 'http:www.baidu.com',
          surl: process.env.VUE_APP_SHORT_URL_HOST + '/AWX39IVU'
        }
      ]
    }
  },
  created () {
    this.loadData()
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.limit = val
      this.loadData()
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.page = val
      this.loadData()
    },
    onAdd () {
      this.$refs.add.open()
    },
    logout () {
      this.$token.set('')
      this.$router.push('/login')
    },
    loadData () {
      surlsList({ page: this.page, limit: this.limit }).then((res) => {
        if (res.code === 0) {
          const arr = res.data
          this.tableData = arr.map((item) => {
            item.surl = process.env.VUE_APP_SHORT_URL_HOST + '/' + item.code
            return item
          })
          this.count = res.count
        }
      })
    },
    handleDel (row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          surlsDel(row.id).then((res) => {
            if (res.code === 0) {
              this.$message.success(res.msg)
              this.loadData()
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  margin: 20px;
  .oper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  .paging {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}
</style>
