import axios from 'axios'
import { Token } from './storage'
import router from '@/router'
import { Message } from 'element-ui'

axios.defaults.baseURL = process.env.VUE_APP_API_URL + '/api'
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.headers.common.Accept = 'application/json'

axios.interceptors.request.use(
  function (config) {
    axios.defaults.headers.common.Authorization = 'Bearer ' + Token.get()
    return config
  },
  function (error) {
    return Promise.reject(error)
  }
)

axios.interceptors.response.use(
  function (response) {
    // 普通错误处理
    if (
      Object.prototype.hasOwnProperty.call(response.data, 'code') &&
      response.data.code !== 0
    ) {
      switch (response.data.code) {
        case 503:
          router.push('/503')
          break
        default:
          if (Object.prototype.hasOwnProperty.call(response.data, 'msg')) {
            Message.error(response.data.msg)
            console.log('response error', response.data)
          }
      }
    }
    return response.data
  },
  function (error) {
    return Promise.reject(error)
  }
)

export default (options, loading = false) => {
  options.loading = loading
  options.test_params = '123'
  return axios.request(options)
}
